.item__buttons {
  margin-top: 13px;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.item__buttons > div {
  visibility: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  width:26px;
  height:26px;
  border-radius: 50%;
  border:2px solid black;
}

.item__buttons > div button, .item__buttons .item__outer-circle {
  visibility: visible;
}

.item__buttons button {
  width:21px;
  height:21px;
  border-radius: 50%;
  border: 1px solid #F0F0F0; 
  cursor: pointer;
}

@media (max-width: 1024px) {
  .item__buttons {
    margin-top: 13px;
  }  
}

@media (max-width: 375px) {
  .item__buttons {
    margin: 32px 0 0 -3px;
  }  

  .item__buttons > div {
    width:14px;
    height:14px;
    border:1px solid black;
  }

  .item__buttons button {
    width:10px;
    height:12px;
    border: 1px solid #F0F0F0; 
  }
}

.item_popular {
  .item__buttons {
    margin-left: -3px;
  }
  .item__buttons > div {
    border:1px solid black;
    width:14px;
    height:14px;
  }
  .item__buttons button {
    width: 10px;
    height: 12px;
  }
}

.item_new {
  .item__buttons {
    margin: 63px 0 0 25px;
  }
  .item__buttons > div {
    margin-right: 2px;
    width:37px;
    height:34px;
  }
  .item__buttons button {
    width:28px;
    height:27px;
  }
}

@media (max-width: 1100px) {
  .item_new {
   .item__buttons {
     margin: 46px 0 0 20px;
   }
  }
}
@media (max-width: 375px) {
  .item_popular {
   .item__buttons {
     margin-top: 17px;
   }
  }
  .item_new {
    .item__buttons {
      margin: 30px 0 0 5px; 
    }
    .item__buttons > div {
      width:14px;
      height:14px;
      border:1px solid black;
    }
  
    .item__buttons button {
      width:10px;
      height:12px;
      border: 1px solid #F0F0F0; 
    }
   }
   
}


