.home-page {
  .title {
    font-size: 45px;
    font-weight: bold;
    float: left;
    // clear: both;
  } 
  .popular {
    height: 500px;
  }
  
  .new {
    height: 620px;
  }
} 

@media (max-width: 1100px) {
  .home-page {
    margin: 0 72px 0 68px;
  }
} 
@media (max-width: 375px) {
  .home-page {
    margin: 0;
    .title {
      margin: 9px 0 0 10px;
      font-family: GraphikSemiBold;
      font-size: 24px;
    }
  }
  .popular {
    height: 370px;
  }
} 