.filters {
  height:70px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
  background-color: #F4F5F0;
}

.filters button:first-child { 
  margin-left: 80px;
}

.filters button {
  width: 100px;
  background-color: white;
  color: #6FA69B;
  border: 1px solid #6FA69B;
  border-radius: 5px;
  padding: 5px;
  margin-right: 20px;
  text-align: center;
  font-weight: 500;
  font-size: 16px;
  cursor: pointer;
}

.filters button:hover{
  background-color: green;
  color: black;
} 

@media (max-width: 375px) {
  .filters button {
    padding: 1px;
    margin-right: 5px;
  }
}